import React, { useMemo } from 'react';
import styled from 'styled-components';
import useTokenBalance from '../../hooks/useTokenBalance';
import { getDisplayBalance } from '../../utils/formatBalance';

import Label from '../Label';
import Modal, { ModalProps } from '../Modal';
import ModalTitle from '../ModalTitle';
import useSereneFinance from '../../hooks/useSereneFinance';
import TokenSymbol from '../TokenSymbol';
import { Typography } from '@material-ui/core';



const WalletModal = styled.div` 
  border-radius: 40px;
  background-color: rgba(255, 255, 255, 0.4); 
  backdrop-filter:blur(12px); 
  justify-content: center; 
  align-items: center; 
  display: flex; 
  flex-direction: column; 
  box-shadow: 4px 0px 15px 6px rgba(0, 0, 0, 0.5), 0 6px 20px 6px rgba(0, 0, 0, 0.4); 
  padding: 30px; 
  color: #fff; 
`;

const AccountModal: React.FC<ModalProps> = ({ onDismiss }) => {
  const sereneFinance = useSereneFinance();

  const sereneBalance = useTokenBalance(sereneFinance.aFish);
  const displayTombBalance = useMemo(() => getDisplayBalance(sereneBalance), [sereneBalance]);
  
  const serenev2Balance = useTokenBalance(sereneFinance.aFish);
  const displaySereneBalance = useMemo(() => getDisplayBalance(serenev2Balance), [serenev2Balance]);

  const tshareBalance = useTokenBalance(sereneFinance.whale);
  const displayTshareBalance = useMemo(() => getDisplayBalance(tshareBalance), [tshareBalance]);

  return (

      <WalletModal>
      <Typography style={{ fontSize:'1.75rem', color:'white'}}>
        My Wallet
        </Typography>

      <Balances>
        <StyledBalanceWrapper>
          <TokenSymbol symbol="aFish" size={96} />
          <StyledBalance>
            <StyledValue>{displaySereneBalance}</StyledValue>
            <Label text="AFISH Available" />
          </StyledBalance>
        </StyledBalanceWrapper>

        {/* <StyledBalanceWrapper>
          <TokenSymbol symbol="aFish" />
          <StyledBalance>
            <StyledValue>{displaySereneBalance}</StyledValue>
            <Label text="aFish Available" />
          </StyledBalance>
        </StyledBalanceWrapper> */}

        <StyledBalanceWrapper>
          <TokenSymbol symbol="whale" size={96}/>
          <StyledBalance>
            <StyledValue>{displayTshareBalance}</StyledValue>
            <Label text="WHALE Available" />
          </StyledBalance>
        </StyledBalanceWrapper>

      </Balances>
      </WalletModal>

  );
};

const StyledValue = styled.div`
  //color: ${(props) => props.theme.color.grey[300]};
  font-size: 30px;
  font-weight: 700;
`;

const StyledBalance = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`;

const Balances = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: ${(props) => props.theme.spacing[4]}px;
`;

const StyledBalanceWrapper = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 0 ${(props) => props.theme.spacing[3]}px;
`;

export default AccountModal;
