import React, { useMemo } from 'react';
import clsx from 'clsx';
import { Link } from 'react-router-dom';
import {
  AppBar,
  Box,
  Drawer,
  IconButton,
  Toolbar,
  Typography,
  useMediaQuery,
  List,
  ListItem,
  ListItemText,
  Divider,
} from '@material-ui/core';
import TokenSymbol from '../../components/TokenSymbol';

import ListItemLink from '../ListItemLink';

import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AccountButton from './AccountButton';
import {ChainSelect} from "../ChainSelect";
import useSereneV2Stats from '../../hooks/useSereneStats';
import usepebbleStats from '../../hooks/usePebbleStats';
import logo from '../../assets/img/logo.png'

const useStyles = makeStyles((theme) => ({
  '@global': {
    ul: {
      margin: 0,
      padding: 0,
      listStyle: 'none',
    },
  },
  appBar: {
    color: '#fff',
    backgroundColor: '#1e2025',
    boxShadow: '0 0 0 1px #22282f',
    backdropFilter:'blur(12px)', 
    // filter: 'blur(0.1px)',
    // borderBottom: `1px solid ${theme.palette.divider}`,
    padding: '4px',
    borderRadius:'1px', 
  
  },
  drawer: {
    width: 240,
    flexShrink: 0,
   
  },
  drawerPaper: {
    width: 240,
    backgroundColor: 'rgba(255, 255, 255, 0.1)',
    boxShadow: '0 1px 25px 1px #000',
    backdropFilter:'blur(12px)', 
  },
  hide: {
    display: 'none',
  },
  toolbar: {
    flexWrap: 'wrap',
  },
  toolbarTitle: {
    fontFamily: '"Roboto"',
    fontSize: '2rem',
    flexGrow: 1,
  },
  link: {
    color: '#fff',
    fontSize: '1.1rem',
    margin: theme.spacing(1, 2),
    textDecoration: 'none',
    '&:hover': {
      textDecoration: 'none',
    },
  },
  brandLink: {
    textDecoration: 'none',
    fontWeight:'bold', 
    fontSize:'1.75rem', 
    color: '#fff',
    '&:hover': {
      textDecoration: 'none',
    },
  },
}));

const Nav = () => {
  const matches = useMediaQuery('(min-width:900px)');
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);


  return (
    <AppBar position="sticky" elevation={0} className={classes.appBar}>
      <Toolbar className={classes.toolbar}>
        {matches ? (
          <>
            <Box className={classes.toolbarTitle}>
              <img src={logo} width="200px"/>
              {/* <a className={ classes.brandLink } href="/logo.png">AFish Finance</a> */}
              <Link to="/" color="white" className={classes.brandLink}> 
                
              </Link>
            </Box>
            
            {/* <ChainSelect list={chains} selected={currentChain} handler={handleChainChange} noBorder={true}/> */}
            <AccountButton text="Connect" />
          </>
        ) : (
          <>
            {/* <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              className={clsx(open && classes.hide)}
            >
              <MenuIcon />
            </IconButton> */}
            <Typography variant="h6" noWrap>
              
            </Typography>

            
          </>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default Nav;
