import { useCallback, useEffect, useState } from 'react';
import useSereneFinance from './useSereneFinance';
import { LPStat, VestingStat } from '../serene-finance/types';
import config from '../config';

const useVestingStats = () => {
  const [stat, setStat] = useState<VestingStat>();
  const sereneFinance = useSereneFinance();

  const fetchCashPrice = useCallback(async () => {
    if(document.hasFocus())
      setStat(await sereneFinance.getVestingStat());
  }, [sereneFinance]);

  useEffect(() => {
    fetchCashPrice().catch((err) => console.error(`Failed to fetch veseting infomation: ${err.stack}`));
    const refreshInterval = setInterval(fetchCashPrice, config.refreshInterval);
    return () => clearInterval(refreshInterval);
  }, [setStat, sereneFinance, fetchCashPrice]);

  return stat;
};

export default useVestingStats;
