import React, { useMemo } from 'react';
import Page from '../../components/Page';
import CountUp from 'react-countup';
import styled from 'styled-components';
import { Box, Card, Button, CardContent, Grid, Paper, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import useVestingStats from '../../hooks/useVestingStats';
import useClaimVested from '../../hooks/useClaimVested';

const FishCard = styled.div` 
  border-radius: 10px;
  background-color: #22282f; 
  backdrop-filter:blur(1px); 
  justify-content: center; 
  align-items: center; 
  display: flex; 
  flex-direction: column; 
  box-shadow: 0px 0px 1px 2px #22282f, 0 0px 0px 0px #22282f; 
  padding: 30px; 
  color: #fff; 
`;
const useStyles = makeStyles((theme) => ({
  button: {
    [theme.breakpoints.down('415')]: {
      marginTop: '10px', },
  },
  textCard: {
    marginTop: '2rem',
    marginLeft: '1rem',
    marginRight: '1rem',
    padding: '1rem',
    
  },
  titleCard: {
    marginTop: '2rem',
    marginLeft: '1rem',
    marginRight: '1rem',
    padding: '1rem',
    [theme.breakpoints.up('md')]: {
      marginTop: '2rem',
      marginLeft: '5rem',
      marginRight: '5rem',
      padding:'4rem', 
    },
  },
}));

const Home = () => {
  const classes = useStyles();
  const vestingStat = useVestingStats();
  const {onStake} = useClaimVested();
  return (
    <Page style={{backgroundColor:'#00001b'}}>
      <Typography className="shadowtext" variant="h3" align="center" style={{ marginTop: '25px'}}>CLF Vested Distribution</Typography>
      <Grid container spacing={1}>
        <Grid
          item xs={6}
          direction="row"
          style={{ justifyContent: 'center', alignItems:'center', paddingLeft:'3rem', color: '#cccccc', paddingright:'3rem'  }} >
            <FishCard className={classes.textCard} 
              style={{ padding:'1rem'}}>
                    <Typography className="shadowtext" marginTop="4" justifyContent="center" alignItems="center" 
                    marginBottom="4" 
                    style={{ fontSize:"30px" }}>
                      Total Amount to Claim
                      </Typography>
                      <Typography  className="shadowtext" >
                    <CountUp style={{ fontSize: '25px' }} decimals={2} end={vestingStat?vestingStat.totalClaim:0} separator="," suffix=" CLF" />
                    </Typography>
            </FishCard>
        </Grid>
        <Grid
          item xs={6}
          direction="row"
          style={{ justifyContent: 'center', alignItems:'center', paddingLeft:'3rem', color: '#cccccc', paddingright:'3rem'  }} >
            <FishCard className={classes.textCard} 
              style={{ padding:'1rem'}}>
                    <Typography className="shadowtext" marginTop="4" justifyContent="center" alignItems="center" 
                    marginBottom="4" 
                    style={{ fontSize:"30px"}}>
                      Claimed Amount
                      </Typography>
                      <Typography  className="shadowtext" >
                    <CountUp style={{ fontSize: '25px' }} decimals={2} end={vestingStat?vestingStat.claimed:0} separator="," suffix=" CLF" />
                    </Typography>
            </FishCard>
        </Grid>
      </Grid>
      <Grid container>
        <Grid
          item xs={6}
          direction="row"
          style={{ justifyContent: 'center', margin: 'auto', alignItems:'center', paddingLeft:'3rem', color: '#cccccc', paddingright:'3rem'  }} >
            <FishCard className={classes.textCard} 
              style={{ padding:'1rem'}}>
                    <Typography className="shadowtext" marginTop="4" justifyContent="center" alignItems="center" 
                    marginBottom="4" 
                    style={{ fontSize:"30px"}}>
                      Claimable Amount
                      </Typography>
                      <Typography  className="shadowtext" >
                    <CountUp style={{ fontSize: '25px' }} decimals={2} end={vestingStat?vestingStat.claimable:0} separator="," suffix=" CLF" />
                    </Typography>
                    <Button class="fishbtn" onClick={onStake}>Claim vested amount</Button>
            </FishCard>
        </Grid>
      </Grid>
    </Page>
  );
};

export default Home;
