import { useCallback } from 'react';
import useSereneFinance from './useSereneFinance';
import { Bank } from '../serene-finance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';
import { parseUnits } from 'ethers/lib/utils';

const useClaimVested = () => {
  const sereneFinance = useSereneFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleStake = useCallback(
    () => {
      handleTransactionReceipt(
        sereneFinance.claimVested(),
        `Claim vested ${sereneFinance.myAccount}`,
      );
    },
    [sereneFinance, handleTransactionReceipt],
  );
  return { onStake: handleStake };
};

export default useClaimVested;
